
const dbName = 'myTest';
const version = 1;

let promisify = store => new Promise((res) => store.onsuccess = e => res(e.target.result));

class DB {
    constructor(name) {
        this._name = name;
        const request = indexedDB.open(dbName, version);
        request.onsuccess = (e) => {
            this.db = e.target.result;
            console.log('连接成功！');
        }
        request.onupgradeneeded = (e) => {
            this.db = e.target.result;
            if(!this.db.objectStoreNames.contains(name)){
                this.db.createObjectStore(name,{keyPath: 'id', autoIncrement: true});
            }
        }
    }

    get store() {
        const tx = this.db.transaction(this._name, 'readwrite');
        return tx.objectStore(this._name);
    }

    add(data) {
        this.store.put(data);
    }

    update(data) {
        this.store.delete(data.id).onsuccess = () => {
            this.store.put(data);
        }
    }

    findById(id) {
        return promisify(this.store.get(id));
    }

}

export default DB;