<template>
  <div id="app">
    <div class="wrapper">
      <div class="content">
          <section v-if="currentPage == 0">
            <page-one />
          </section>
          <section v-if="currentPage == 1">
            <page-two />
          </section>
          <section v-if="currentPage == 2">
            <page-3 />
          </section>
          <section v-if="currentPage == 3">
             <page-three />
          </section>
          <section v-if="currentPage == 4">
            <page-four />
          </section>
          <section v-if="currentPage == 5">
            <page-five />
          </section>
          <section v-if="currentPage == 6">
            <page-six />
          </section>
          <section v-if="currentPage == 7">
            <page-seven />
          </section>
          <section v-if="currentPage == 8">
            <page-eight />
          </section>
          <section v-if="currentPage == 9">
            <page-nine />
          </section>
          <section v-if="currentPage == 10">
            <page10 />
          </section>
          <section v-if="currentPage == 11">
            <page11 />
          </section>
          <section v-if="currentPage == 12">
            <page12 />
          </section>
      </div>
      <progress-line :width="width"/>
      <div class="btn-content">
        <button 
          class="navigate-left" 
          :disabled="currentPage === 0" 
          @click="goPage(false)">
        </button>
        <button 
          class="navigate-right" 
          :disabled="currentPage === pageTotal" 
          @click="goPage(true)">
        </button>
      </div>
    </div>
  </div>
</template>

<script>
import progressLine from "./components/progress";
import pageOne from "./components/page-one";
import pageTwo from "./components/page-two";
import pageThree from "./components/page-three";
import pageFour from "./components/page-four";
import pageFive from "./components/page-five";
import pageSix from "./components/page-six";
import pageSeven from "./components/page-seven";
import pageEight from "./components/page-eight";
import pageNine from "./components/page-nine";
import page10 from "./components/page-10";
import page11 from "./components/page-11";
import page12 from "./components/page-12";
import page3 from "./components/page-3";

export default {
    name: "App",
    components: {
        progressLine,
        pageOne,
        pageTwo,
        pageThree,
        pageFour,
        pageFive,
        pageSix,
        pageSeven,
        pageEight,
        pageNine,
        page10,
        page11,
        page12,
        page3,
    },
    data() {
      return {
        width: 0,
        pageTotal: 11,
        currentPage: 0,
      }
    },
    mounted() {
      window.addEventListener('keydown', this.keyEvent, false);
    },
    methods: {
      goPage(status = true, num = 1) {
        this.currentPage = status ? this.currentPage += num : this.currentPage -= num;
        const winWidth = document.body.clientWidth;
        const unitWidth = Math.ceil(winWidth / this.pageTotal);
        this.width = unitWidth * this.currentPage;
      },
      keyEvent(e) {
        if (e.which === 39) {
          this.goPage(true);
        }
        if (e.which === 37) {
          this.goPage(false);
        }
      }
      // goCurrentPage(num) {
      //   this.goPage(true, num);
      // }
    },
};
</script>

